.AzkarDayNight {
  &__tabList {
    position: sticky;
    top: -0.5rem;
    background-color: var(--color-dark1-theme);
    z-index: 1;
  }

  &__rowContent {
    text-align: center;

    &:not(:last-of-type) {
      border-bottom: 1px solid var(--color-border-theme);
      padding-bottom: var(--default-padding);
    }

    &:not(:last-child) {
      margin-bottom: var(--default-padding);
    }
  }

  &__zekr {
    margin: var(--default-padding) 0;
    background-color: #fff;
    background-clip: text;
    color: rgba(255, 255, 255, 0);
    padding: inherit;
    line-height: 2.5;
  }
  &__basmalla {
    background-color: #fff;
    background-clip: text;
    color: rgba(255, 255, 255, 0);
    padding: inherit;
  }
}
