@import "../../mixins";

.verseContent {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--color-light1);

  &__text {
    position: relative;
    border-radius: var(--default-border-radius);
    background-color: var(--color-dark1-opacity-45);
    padding: var(--default-padding);
    backdrop-filter: blur(var(--default-blur));
    text-align: center;
    min-width: 400px;
    max-width: calc(100vw - 280px);
    max-height: 50vh;
    overflow: auto;
    font-size: 20px;
    line-height: 35px;
    // border: 1px solid rgba(253, 225, 160, .5);

    &--ar {
      direction: rtl;
      font-size: 30px;
      line-height: 2.5;
      font-family: var(--verse-font-family);
      background-color: #fff;
      background-clip: text;
      color: rgba(255, 255, 255, 0);
    }
  }

  &__iconsContent {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: calc(var(--default-padding) / 2);
  }

  &__moon {
    position: absolute;
    bottom: 73px;
    left: -15px;
    width: 80px;
    height: 80px;
    background: url("../../assets/imgs/moon.svg") no-repeat;
    background-size: contain;

    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: inherit;
      filter: blur(10px);
      z-index: -1;
    }
  }
}
