.CustomAdhkar {
  &__rowContent {
    position: relative;
    text-align: center;

    &:not(:last-of-type) {
      border-bottom: 1px solid var(--color-border-theme);
      padding-bottom: var(--default-padding);
    }

    &:not(:last-child) {
      margin-bottom: var(--default-padding);
    }

    &:hover {
      .CustomAdhkar__editButton {
        opacity: 1;
      }
    }
  }

  &__zekr {
    margin: var(--default-padding) 0;
  }

  &__editButton {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;

    button {
      backdrop-filter: blur(2px);
    }
  }

  &__menuText {
    font-size: var(--font-size-small);
  }
}
