@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

@font-face {
  font-family: AmiriQuran-Regular;
  src: url("assets/font/AmiriQuran-Regular.ttf");
}

@font-face {
  font-family: Tajawal_Medium;
  src: url("assets/font/Tajawal-Medium.ttf");
}

:root {
  --direction-en: ltr;
  --direction-ar: rtl;
  --default-direction: var(--direction-en);
  --en-font-family: "Roboto";
  --ar-font-family: "Tajawal_Medium";
  --ar-isActive: 0; /*We are using this value to switch margin value between left/right*/
  --verse-font-family: "AmiriQuran-Regular", serif;
  --default-font-family: var(--en-font-family);
  --default-padding: 1rem;
  --default-border-radius: 0.375rem;
  --default-blur: 0.1rem;
  /*--color-dark1: #292b2d;
  --color-dark2: #2f3234;
  --color-dark3: #4f4f4f;*/
  --color-dark1: #2d3748;
  --color-dark1-opacity-45: rgba(130, 125, 125, 0.45);
  --color-dark2: #4a5568;
  --color-dark3: #718096;
  --color-light1: #fff;
  --color-light2: #e0e0e0;
  --color-light3: #a3a3a3;
  --color-border: var(--color-dark3);
  /*Those colors will be changed between themes - Default color is Dark*/
  --color-dark1-theme: var(--color-dark1);
  --color-light1-theme: var(--color-light1);
  --color-border-theme: var(--color-dark3);
  /*End*/
  --color-green-light: #b2ff59;
  --color-dominant: #7ed321;
  --color-green: #7ed321;
  --color-red: #f90332;
  --color-error: #df756c;
  --color-blue: #59a7ec;
  --font-size-xxSmall: 0.625rem; /* 10px */
  --font-size-xSmall: 0.75rem; /* 12px */
  --font-size-small: 0.875rem; /* 16px */
  --font-size-regular: 1rem; /* 16px */
  --font-size-large: 1.125rem; /* 18px */
  --font-size-xLarge: 1.25rem; /* 20px */
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-semi-bold: 500;
  --font-weight-bold: 700;
  --animation-timing: 0.2s;
  --text-shadow: 0rem 0rem 3px rgba(0, 0, 0, 0.9);
}

::-webkit-scrollbar {
  width: 0.6rem;
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.3);
  border: 0.1rem solid rgba(0, 0, 0, 0.2);
  border-radius: 1.5rem;
}

::-webkit-scrollbar-track {
  background: transparent;
  margin: 0.5rem;
}

html {
  background: #353535;
}

body {
  font-size: var(--font-size-regular);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*:focus {
  /*remove the outline that chakra-ui adds to elements*/
  /*box-shadow: none !important;*/
}

.chakra-ui-light {
  --color-dark1-theme: var(--color-light1);
  --color-light1-theme: var(--color-dark1);
  --color-border-theme: var(--color-light2);
}
