.toggle {
  display: flex;
  width: 51px;
  height: 31px;
  background-color: var(--color-light3);
  border-radius: 18px;
  align-items: center;
  padding: 0 4px;
  cursor: pointer;
  transform: scale(0.8);
  transition: all var(--animation-timing);

  &__circle {
    width: 24px;
    height: 24px;
    border-radius: 15px;
    background-color: var(--color-light1);
    transition: all var(--animation-timing);
  }

  &--active {
    background-color: var(--color-dominant);

    .toggle__circle {
      margin-left: 19px;

      [dir="rtl"] & {
        margin-left: 0;
        margin-right: 19px;
      }
    }
  }
}
