.QuickLinks {
  &__closeButton {
    opacity: 0;
    visibility: hidden;
  }

  &__row {
    &:hover {
      .QuickLinks__closeButton {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__newTab {
    display: flex;
    align-items: center;
    padding: 0 7px 7px 7px;
    margin-bottom: 7px;
    justify-content: space-between;
    border-bottom: 1px solid var(--color-border-theme);

    &Toggle {
      transform: scale(0.8);
    }
    &Text {
      flex: 1;
      text-align: center;
    }
  }
}
