@import "../../mixins";

.verseDetails {
  text-transform: capitalize;
  backdrop-filter: blur(var(--default-blur));
  direction: var(--default-direction);
  &--text {
    padding: var(--default-padding);
  }
  &--ar {
    background-color: #fff;
    background-clip: text;
    color: rgba(255, 255, 255, 0);
    padding: var(--default-padding);
  }
}
