.sentence {
  position: absolute;
  width: 100%;
  text-align: center;
  font-size: var(--font-size-small);
  z-index: -1;
  color: var(--color-light1);
}

.word {
  display: inline-block;
  background: #246a00;
  border-radius: 3px;
  padding: 2px 4px;
}

.date {
  display: inline-block;
  font-family: var(--en-font-family);
  background: #b20000;
  border-radius: 3px;
  padding: 2px;
}

.palestineFlag {
  max-width: 36px;
  max-height: 36px;
  display: inline-block;
}
