.Header {
  position: absolute;
  display: flex;
  top: 0;
  font-weight: var(--font-weight-bold);
  padding: calc(var(--default-padding) / 2);
  width: 100%;
  z-index: 2;

  &__topRight {
    display: flex;
    //backdrop-filter: blur(var(--default-blur));
    margin-left: auto;
  }

  &__fanos {
    position: relative;
    top: -10px;
    margin: 0 -20px;
    z-index: -1;
    transform-origin: top;
    animation: swing 1s infinite alternate ease-in-out;

    &--right {
      top: -20px;
      width: 50px;
      animation: swing 1s 0.3s infinite alternate ease-in-out;
    }
  }
}

@keyframes swing {
  from {
    transform: rotate(-3deg);
  }
  to {
    transform: rotate(3deg);
  }
}
